.header {
  width: 100%;
  background-color: rgba(128, 128, 128, .4);
  height: 10vh;
  border-bottom: 2px solid $color-sc-lighter;

  .nav {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    //border: 1px dashed #3c3c3c;

    &__logo {
      display: inline-block;
      font-size: 0;
      transition: 0.4s all;

      img {
        height: 9vh;
        width: auto;
        margin-left: 1vw;
        margin-right: 1vw;
      }

      &:hover {
        transform: rotate(15deg) ;
      }
    }

    &__title {
      font-size: 1.5rem;
      text-shadow: 0 0 20px rgba(0,0,0,0.2);
      letter-spacing: 1px;
      line-height: 3.2vh;
      font-weight: bold;
      //width: 25vw;
      //margin-left: 1vw;
    }

    &__menu {
      width: 50vw;
      height: 10vh;
      display: flex;
      flex-grow: 1;
      flex-shrink: 1;
      flex-flow: row wrap;
      justify-content: center;
      align-items: center;

      li {
        display: flex;
        flex-shrink: 1;
        align-items: center;
        justify-content: center;
        height: 70%;
        background-color: #ff8d3e;
        text-align: center;
        font-size: 1.2rem;
        padding: 0.5vw;
        margin: 0 0.2em;
        border: 3px solid grey;
        transition: 0.3s all;
        //width: 7vw;
        &:hover {
          //background-color: lighten($color-pr-lighter, 10%);
          background-color: lighten($color-pr-lighter, 7%);
          cursor: pointer;
        }

        a {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 100%;
          text-align: center;
          text-decoration: none;
          color: #000;
          outline: none;
          font-size: 1.3rem;
          text-shadow: 0 0 20px rgba(255,255,255,.7);
        }
      }
    }

    .login-register-btn {
      margin-right: 1vw;
      height: 8vh;
      transition: 0.3s all;

      &:hover {
        cursor: pointer;
        transform: scale(1.1);
      }

      a {
        display: inline-block;
        height: inherit;


        img {
          display: inline-block;
          font-size: 0;
          height: inherit;
          width: auto;

        }
      }
    }
  }
}





