.main-slider {
  background-color: rgba(0, 0, 0, .7);
  margin: 1vh auto;
  width: 85%;
  height: 50vh;
  border: 5px solid $color-sc-darker;
  outline: none;

  .slider-link {
    position: relative;

    .slider-title {
      //display: inline-block;
      background-color: rgba($color-sc-lighter, 0.5);
      position: absolute;
      outline: none;
      text-decoration: none;
      color: #fff;
      font-size: 2rem;
      padding: 0.8vw;
      z-index: 999;
      transition: 0.3s all;
      text-shadow: 0 0 20px rgba(0,0,0,0.3);


      &:hover {
        background-color: rgba($color-pr-lighter, 0.7);
      }
    }
  }

  .slick-list {
    margin: 0 auto;
    height: 100%;
    max-width: 100%;
    outline: none;

    .slick-track {
      height: 100%;
      margin: 0 auto;
      outline: none;

      .slick-slide {
        margin: 0 auto;
        height: 100%;
        max-width: 100%;
        outline: none;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;

        .slider-img {
          display: block;
          max-width: 100%;
          max-height: 100%;
          margin: 0 auto;
          //padding: 30px 50px;
          outline: none;
          transition: 0.4s all;

          &:hover {
            transform: scale(1.1) rotate(1deg);
          }
        }
      }
    }
  }

}

.slick-arrow {
  position: absolute;
  top: 45%;
  background-color: rgba($color-sc-darker, 0.5);
  height: 50px;
  width: 50px;
  color: transparent;
  font-size: 0;
  outline: none;
}

.slick-next {
  right: -70px;
}

.slick-prev {
  left: -70px;
}


.slick-dots {
  margin-top: 2vh;

  li {
    display: inline-block;
    padding: 0 0.3vw;

    button {
      color: #000;
      font-weight: bold;
      font-size: 0.8rem;
      background: $color-pr-lighter;
      border: 4px solid $color-sc-darker;
      outline: none;
      width: 28px;
      height: 28px;

      &:hover{
        cursor: pointer;
      }
    }
  }
}