.quiz-container {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  overflow: hidden;
  width: 100%;
  min-height: 100vh;
  text-align: center;
  background-image: url("../img/42d8ecd7-c26e-4f1b-ad9b-9684bd322d28.png");
  background-repeat: repeat-y;
  background-size: cover;
  background-position: center;

  h1 {
    padding-top: 1vh;
  }

  .quiz-column {
    min-height: 100vh;
    width: 60%;
    background-color: rgba($color-sc-darker, .7);
    padding-bottom: 5vh;

    .quiz-title {
      text-shadow: 0 0 20px rgba(0,0,0,0.3);
      font-size: 1.8rem;
      font-weight: bold;
      margin: 3vh 0 1vh 0;
      padding: 2vh;
      //color: #3197EE;
      background-color: rgba($color-pr-darker, .6);

    }

    p:nth-child(3) {
      text-shadow: 0 0 20px rgba(0,0,0,0.3);
      font-size: 1.1rem;
      background-color: rgba($color-pr-lighter, .6);
      padding: 0.8vh;

    }

    .question-form {
      position: relative;
      padding-top: 2vh;
      //display: flex;
      //flex-flow: row wrap;
      //background-color: rgba($color-pr-lighter, .2);
      width: auto;
      height: auto;

      //margin: 0 auto;

      .question-body {
        padding: 1vh;
        //background-color: red;
        width: 70%;
        margin: 0 auto;
        //border: 1px solid black;
        background-color: rgba($color2, .3);

        .question {
          text-shadow: 0 0 20px rgba(0,0,0,0.3);
          font-size: 1.2rem;
          padding-bottom: 1vh;
          font-weight: bold;
          line-height: 2.5vh;
          letter-spacing: 0.1vw;

        }
      }
    }
  }
}

