input[type='radio'] {
  position: absolute;
  opacity: 0;
  }

.label-radio {
  display: inline-block;
  text-shadow: 0 0 20px rgba(0,0,0,0.3);
  padding: 0.6vh;
  font-size: 1.1em;
  font-weight: bold;
  font-style: italic;
  letter-spacing: 0.1vw;
  transition: 0.2s all;

  &:hover {
    cursor: pointer;
    color: lighten($color-pr-lighter, 10%);
  }

  &:before {
    position: relative;
    content:'';
    background: $color-sc-lighter;
    border-radius: 100%;
    border: 2px solid $color-pr-lighter;
    width: 0.6em;
    height: 0.6em;
    display: inline-block;
    top: 0.1em;
    left: -0.1em;
    margin-right: .8em;
    vertical-align: top;
    cursor: pointer;
    text-align: center;
    transition: all 250ms ease;

  }
}


